@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}

body {
  background: linear-gradient(#ea8efc, #E0C3FC);
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}

h4,
h5,
h6,
label {
  font-weight: 100;
  font-family: Arial, Helvetica, sans-serif;
}

.form-group label {
  font-weight: bold;
}

.btn-primary {
  background-color: rgb(218, 107, 190) !important;
  border-color: black !important;
  color: black !important;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

h3.shrinkr-title {
  font-size: 50px;
  font-weight: bold;
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  padding-left: 10px;
  padding-right: 10px;

}

.auth-inner {
  width: 50%;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #ff16f3;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 1;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 500;
}


.form-group {
  padding: 30px 0 30px 0;
}

.generatedurl {
  margin-top: 20px;
}

.generatedurl span {
  font-family: 'Fira Sans', sans-serif;
}
.App {
  text-align: left;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #0e0d0d;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

